import { Breadcrumb, Layout } from 'antd';
import React from 'react';
import { useLocation } from 'react-router-dom';

import './index.css';

const { Content } = Layout;

const Detail: React.FC = () => {
	// state
	const parmas = useLocation();
	const parmasData = parmas.state;

	return (
		<Content style={{ padding: '0 50px' }}>
			<Breadcrumb style={{ margin: '16px 0' }}>
				<Breadcrumb.Item href="/#/homepage"></Breadcrumb.Item>
				<Breadcrumb.Item></Breadcrumb.Item>
			</Breadcrumb>

			<div className="site-layout-content">
				<div className="detailDataItem">
					<span>: </span>
					<img src={parmasData.photo} className="detailPeosenPhoto" />
				</div>
				<div className="detailDataItem">
					<span>: </span>
					{parmasData.name}
				</div>
				<div className="detailDataItem">
					<span>: </span>
					{parmasData.project}
				</div>
				<div className="detailDataItem">
					<span>: </span>
					{parmasData.grade}
				</div>
				<div className="detailDataItem">
					<span>: </span>
					{parmasData.sex ? 'male' : 'female'}
				</div>
				<div className="detailDataItem">
					<span>: </span>
					{parmasData.phone}
				</div>
				<div className="detailDataItem">
					<span>: </span>
					{parmasData.email}
				</div>
			</div>
		</Content>
	);
};

export default Detail;
