import { Button, message, Modal, Popover, Table, Tag } from 'antd';
import type { ColumnsType } from 'antd/es/table';
import React, { useEffect, useState } from 'react';
import { ExclamationCircleOutlined, SettingOutlined } from '@ant-design/icons';
import { Link } from 'react-router-dom';
import axios from 'axios';

import EditDataForm from '../EditDataForm';
import './style.css';

const { confirm } = Modal;

interface DataType {
	id: React.Key;
	photo: string;
	name: string;
	project: string;
	grade: string;
	sex: number;
	phone: string;
	email: string;
}

interface TableParams {
	pagination: any;
}

const DataTable = (props: {
	stuListData: any;
	setStuListData: Function;
	tableOps: number;
	setTableOps: Function;
	searchString: string;
	tableParams: TableParams;
	setTableParams: Function;
}) => {
	// 
	const columns: ColumnsType<DataType> = [
		{
			title: '',
			dataIndex: 'photo',
			key: 'photo',
			render: (photoSrc) => {
				return <img src={photoSrc} className="PeosenPhoto" />;
			},
		},
		{
			title: '',
			dataIndex: 'name',
			key: 'name',
		},
		{
			title: '',
			dataIndex: 'project',
			key: 'project',
		},
		{
			title: '',
			dataIndex: 'grade',
			key: 'grade',
		},
		{
			title: '',
			key: 'sex',
			dataIndex: 'sex',
			render: (_, { sex }) => {
				if (sex == 0) {
					return <Tag color="geekblue"></Tag>;
				}
				return <Tag color="green"></Tag>;
			},
		},
		{
			title: '',
			dataIndex: 'phone',
			key: 'phone',
		},
		{
			title: '',
			dataIndex: 'email',
			key: 'email',
		},
		{
			title: '',
			dataIndex: 'key',
			key: 'setOps',
			render: (_text, record, _index) => {
				return (
					<Popover
						placement="bottom"
						content={opsHoverButtonContent(record)}
						trigger="hover">
						<Button className="opsHoverButtonContent">
							<SettingOutlined />
						</Button>
					</Popover>
				);
			},
		},
	];

	// 
	const handleTableChange = (pagination: any) => {
		props.setTableParams({
			pagination,
		});
	};
	// 
	const showDeleteConfirm = (stuData: DataType) => {
		confirm({
			title: '',
			icon: <ExclamationCircleOutlined />,
			content: ` ${stuData.name} `,
			okText: '',
			cancelText: '',
			onOk() {
				axios
					.post('/api/stu/delete', { id: stuData.id })
					.then((res) => {
						console.log(res.data);
						if (res.data.code === 0) {
							console.log('');
							message.success('');
							props.setTableOps(props.tableOps + 1);
							console.log(
								'',
								Math.ceil((props.stuListData.total - 1) / 5)
							);
							console.log('', props.tableParams.pagination.current);

							// 
							const maxPage = Math.ceil((props.stuListData.total - 1) / 5);
							if (props.tableParams.pagination.current >= maxPage) {
								props.setTableParams({
									pagination: {
										current: maxPage,
										pageSize: 5,
									},
								});
							}
						} else {
							console.log('');
							message.info('');
						}
					})
					.catch((err) => {
						console.log('' + err);
					});
			},
			onCancel() {
				console.log('');
				message.success('');
			},
		});
	};
	// 
	const opsHoverButtonContent = (_record: DataType) => {
		return (
			<div className="opsHoverButtonCantainer">
				<Button className="opsHoverButton">
					<Link to={'/homepage/detail'} state={_record}>
						
					</Link>
				</Button>
				<Button
					className="opsHoverButton"
					onClick={() => {
						setOpenEditDataBox(true);
						setEditStuData(_record);
					}}>
					
				</Button>
				<Button
					onClick={() => showDeleteConfirm(_record)}
					type="dashed"
					style={{ border: 0 }}>
					
				</Button>
			</div>
		);
	};
	/* 
	   useEffect
	   
	   searchsearch */
	useEffect(() => {
		axios
			.get(
				`/api/stu/list?search=${props.searchString}&page=${props.tableParams.pagination.current}`
			)
			.then((res) => {
				if (res.data.code === 0) {
					const newStuData = res.data;
					props.setStuListData(newStuData);
				} else if (res.data.code === -2) {
					//
					console.log(res.data);
				}
			})
			.catch((err) => {
				console.log('' + err);
			});
	}, [props.tableParams, props.tableOps]);

	// 
	const [openEditDataBox, setOpenEditDataBox] = useState(false);
	const [editStuData, setEditStuData] = useState({});

	const onCreate = (values: DataType) => {
		// idkeyid
		axios
			.post('/api/stu/update', {
				id: values.id,
				photo: '/stuphoto.png',
				name: values.name,
				project: values.project,
				grade: values.grade,
				sex: String(values.sex),
				phone: values.phone,
				email: values.email,
			})
			.then((res) => {
				if (res.data.code === 0) {
					setOpenEditDataBox(false);
					message.success('');
					// 
					props.setTableOps(props.tableOps + 1);
				}
				console.log(res.data);
			})
			.catch((err) => {
				console.log('' + err);
			});
	};

	return (
		<>
			<Table
				rowKey="id"
				columns={columns}
				dataSource={props.stuListData.list}
				pagination={{
					showSizeChanger: false,
					defaultCurrent: 1,
					defaultPageSize: 5,
					total: props.stuListData.total,
				}}
				style={{ minWidth: '1000px' }}
				onChange={handleTableChange}
			/>
			<EditDataForm
				open={openEditDataBox}
				onCreate={onCreate}
				onCancel={() => {
					setOpenEditDataBox(false);
				}}
				editStuData={editStuData}></EditDataForm>
		</>
	);
};

export default DataTable;
